* {
  padding: 0;
  margin: 0;
}
body {
  font-family: "Hepta Slab", serif;
}
.app {
  background: tomato;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  height: 100vh;
  width: 100vw;
}
.button {
  padding: 10px 20px;
  font-size: 20px;
  background: #fff;
  border: none;
  outline: none;
  color: tomato;
  cursor: pointer;
  margin: 10px;
}
.demo-heading {
  color: #fff;
  width: 100%;
  text-align: center;
  margin-top: 100px;
}
